import { Button, Grid, Icon, IconButton, Input } from "@material-ui/core";
import CustomTable from "app/component/CustomTable";
import { getAllEmployeesAction } from "app/redux/actions/employeesAction";
import { Breadcrumb } from "egret";
import moment from "moment";
import React, { useCallback, useEffect } from "react";
import { useState } from "react";
import { Helmet } from "react-helmet";
import { useDispatch, useSelector } from "react-redux";
import AddEmployeeDialog from "./AddEmployeeDialog";

const AddEmployee = ({ t }) => {
    const title = t('Dashboard.category');
    const [pagnition, setPagnition] = useState({
        page: 0,
        rowsPerPage: 10,
    })
    const [openDialog, setOpenDialog] = useState(false)
    const dispatch = useDispatch()
    const { employees } = useSelector((state) => state.employees);

    const columns = [
        {
            title: "Thao tác",
            field: 'custom',
            align: 'center',
            maxWidth: '100px',
            render: () => {
                return (
                    <div className="">
                        <IconButton size="small">
                            <Icon fontSize="small" color="primary">
                                edit
                            </Icon>
                        </IconButton>
                        <IconButton size="small">
                            <Icon fontSize="small" color="error">
                                delete
                            </Icon>
                        </IconButton>
                    </div>
                );
            }
        },
        {
            title: "STT",
            align: "center",
            maxWidth: "60px"
        },
        {
            title: "Mã nhân viên",
            field: 'code',
            align: 'center',
            minWidth: '120px',
            maxWidth: '120px'
        },
        {
            title: "Tên nhân viên",
            field: 'name',
            align: 'left',
            minWidth: '200px',
            maxWidth: '200px',
        },
        {
            title: "Ngày sinh",
            field: 'dateOfBirth',
            align: 'center',
            minWidth: '100px',
            maxWidth: '100px'
        },
        {
            title: "Giới tính",
            field: 'gender',
            align: 'center',
            minWidth: '60px',
            maxWidth: '100px'
        },
        {
            title: "Nhóm",
            field: 'team',
            align: 'left',
            minWidth: '160px'
        },
        {
            title: "SĐT",
            field: 'phone',
            align: 'center',
            minWidth: '130px',
            maxWidth: '140px'
        },
        {
            title: "Địa chỉ",
            field: 'address',
            align: 'left',
            minWidth: '200px',
            maxWidth: '250px'
        },
        {
            title: "Trạng thái",
            field: 'submitProfileStatus',
            align: 'left',
            minWidth: '140px'
        },
    ]

    return (
        <div className="analytics m-sm-30">
            <div className="mb-sm-30">
                <Helmet>
                    <title>
                        {title} | {t('web_site')}
                    </title>
                </Helmet>
                <div className="mb-sm-30">
                    <Breadcrumb routeSegments={[
                        { id: 1, name: title },
                        { id: 2, name: t('manage.add_employees') }
                    ]} />
                </div>
            </div>
            <Grid container spacing={3} justify="space-between">
                <Grid item lg={5} md={5} sm={5} xs={12}>
                    <Button
                        className="mb-16 mr-16 align-bottom"
                        variant="contained"
                        color="primary"
                        onClick={() => setOpenDialog(true)}
                    >
                        Thêm mới
                    </Button>
                </Grid>
                <Grid item lg={3} md={4} xs={12} >
                    <Input />
                </Grid>
                <Grid item xs={12}>
                    <CustomTable
                        // data={employees}
                        columns={columns}
                        // totalElements={totalElements}
                        pagnition={pagnition}
                        setPagnition={setPagnition}
                        t={t}
                    />
                </Grid>
            </Grid>
            <AddEmployeeDialog open={openDialog} setOpen={setOpenDialog} />
        </div>
    )
}

export default AddEmployee
