import React, { useState } from "react";
import {
    DialogActions,
    Button,
    IconButton,
    Icon,
    Dialog,
    DialogTitle,
    DialogContent,
    Tab,
    Tabs,
    Grid,
    MenuItem,
    makeStyles,
    Box,
    Typography
} from "@material-ui/core";
import "react-toastify/dist/ReactToastify.css";
import '../../../styles/views/_style.scss';
import { toast } from "react-toastify";
import { SelectValidator, TextValidator, ValidatorForm } from "react-material-ui-form-validator";
import moment from "moment";
import { PhotoCamera } from "@material-ui/icons";

toast.configure({
    autoClose: 2000,
    draggable: false,
    limit: 3,
});

const useStyles = makeStyles({
    titleDialog: {
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        marginBottom: 20,
    },

});

const AddEmployeeDialog = ({ open, setOpen }) => {
    const classes = useStyles()
    const [employeeObject, setEmployeeObject] = useState({
        name: '',
        code: '',
        gender: '',
        dateOfBirth: '',
        address: '',
        team: '',
        image: '',
        phone: '',
        citizenIdentificationNumber: '',
        employeeFamilyDtos: [],
        certificatesDto: [],
        ethnic: '',
        religion: '',
        dateOfIssuanceCard: '',
        placeOfIssueCard: '',
    });

    const handleChangeValue = (e) => {
        const { value, name } = e.target;
        setEmployeeObject({ ...employeeObject, [name]: value });
    }

    const handleCloseDialog = () => {
        setOpen(false);
    }

    return (
        <Dialog
            open={open}
            maxWidth="md"
            fullWidth={true}
        >
            <DialogTitle id="draggable-dialog-title">
                <Box className={classes.titleDialog}>
                    <Typography variant="h5" color="textPrimary"> Tạo mới nhân viên </Typography>
                    <IconButton className="position-absolute r-10 t-10" onClick={handleCloseDialog}>
                        <Icon color="default" title="close">
                            close
                        </Icon>
                    </IconButton>
                </Box>
                <Tabs
                    indicatorColor="primary"
                    textColor="primary"
                    variant="fullWidth"
                    value={1}
                    centered
                >
                    <Tab label="Thông tin nhân viên" value={1} />
                    <Tab label="Thông tin văn bằng" value={2} />
                    <Tab label="Thông tin chứng chỉ" value={3} />
                </Tabs>
            </DialogTitle>
            <DialogContent dividers>
                <ValidatorForm>
                    <Grid container spacing={2}>
                        <Grid container item alignItems="center" direction="column" xs={12} sm={12} md={4} lg={4}>
                            <Grid item className="avatar">
                                <img alt="avatar" src={employeeObject.image} />
                            </Grid>
                            <Grid item>
                                <input
                                    className={"hidden"}
                                    type="file"
                                    accept="image/*"
                                    id="icon-button-file"
                                    name="image"
                                    onChange={handleChangeValue}
                                />
                                <label htmlFor="icon-button-file">
                                    <IconButton color="primary" aria-label="upload picture" component="span">
                                        <PhotoCamera />
                                    </IconButton>
                                </label>
                            </Grid>
                        </Grid>
                        <Grid container item spacing={2} md={8} lg={8} xs={12} sm={12}>
                            <Grid item lg={6} md={6} sm={12} xs={12}>
                                <TextValidator
                                    className="w-100 stylePlaceholder mt-2"
                                    size="small"
                                    label={
                                        <span className="font">
                                            <span className="text-red"> * </span>
                                            Mã nhân viên
                                        </span>}
                                    variant="outlined"
                                    value={employeeObject.code || ""}
                                    onChange={handleChangeValue}
                                    type="text"
                                    name="code"
                                    validators={["required"]}
                                    errorMessages={['required']}
                                />
                            </Grid>
                            <Grid item lg={6} md={6} sm={12} xs={12}>
                                <TextValidator
                                    className="w-100 stylePlaceholder mt-2"
                                    size="small"
                                    label={
                                        <span className="font">
                                            <span className="text-red"> * </span>
                                            Tên nhân viên
                                        </span>}
                                    variant="outlined"
                                    onChange={handleChangeValue}
                                    type="text"
                                    name="name"
                                    value={employeeObject.name}
                                    validators={["required"]}
                                    errorMessages={['this field is required', 'email is not valid']}
                                />
                            </Grid>
                            <Grid item lg={6} md={6} sm={12} xs={12}>
                                <TextValidator
                                    className="w-100 stylePlaceholder"
                                    label={
                                        <span className="font">
                                            <span className="text-red"> * </span>
                                            Ngày sinh
                                        </span>
                                    }
                                    onChange={handleChangeValue}
                                    type="date"
                                    name="dateOfBirth"
                                    value={employeeObject?.dateOfBirth ? moment(employeeObject?.dateOfBirth).format("YYYY-MM-DD") : ""}
                                    fullWidth
                                    variant="outlined"
                                    size="small"
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                    validators={["required"]}
                                    errorMessages={['this field is required', 'email is not valid']}

                                />
                            </Grid>
                            <Grid item lg={6} md={6} sm={12} xs={12}>
                                <SelectValidator
                                    className="w-100 stylePlaceholder"
                                    size="small"
                                    label={
                                        <span className="font">
                                            <span className="text-red"> * </span>
                                            Giới tính
                                        </span>}
                                    variant="outlined"
                                    type="text"
                                    name="gender"
                                    value={employeeObject.gender}
                                    onChange={handleChangeValue}
                                    validators={["required"]}
                                    errorMessages={['this field is required', 'email is not valid']}
                                >
                                    <MenuItem value={1}>Nam</MenuItem>
                                    <MenuItem value={2}>Nữ</MenuItem>
                                    <MenuItem value={3}>Khác</MenuItem>
                                </SelectValidator>
                            </Grid>
                            <Grid item lg={6} md={6} sm={12} xs={12}>
                                <SelectValidator
                                    className="w-100 stylePlaceholder"
                                    size="small"
                                    label={
                                        <span className="font">
                                            <span className="text-red"> * </span>
                                            Nhóm
                                        </span>}
                                    variant="outlined"
                                    type="text"
                                    name="team"
                                    value={employeeObject.team}
                                    onChange={handleChangeValue}
                                    validators={["required"]}
                                    errorMessages={['this field is required', 'email is not valid']}
                                >
                                    <MenuItem value={1}>Nhóm T1</MenuItem>
                                    <MenuItem value={2}>Nhóm T2</MenuItem>
                                    <MenuItem value={3}>Nhóm T3</MenuItem>
                                    <MenuItem value={4}>Nhóm T4</MenuItem>
                                </SelectValidator>
                            </Grid>
                            <Grid item lg={6} md={6} sm={12} xs={12}>
                                <TextValidator
                                    className="w-100 stylePlaceholder"
                                    size="small"
                                    label={
                                        <span className="font">
                                            <span className="text-red"> * </span>
                                            SĐT
                                        </span>
                                    }
                                    variant="outlined"
                                    onChange={handleChangeValue}
                                    type="text"
                                    name="citizenIdentificationNumber"
                                    value={employeeObject.citizenIdentificationNumber}
                                    validators={["required"]}
                                    errorMessages={['this field is required', 'email is not valid']}
                                />
                            </Grid>
                            <Grid item lg={6} md={6} sm={12} xs={12}>
                                <TextValidator
                                    className="w-100 stylePlaceholder"
                                    label={
                                        <span className="font">
                                            <span className="text-red"> * </span>
                                            Này cấp
                                        </span>
                                    }
                                    onChange={handleChangeValue}
                                    type="date"
                                    name="dateOfIssuanceCard"
                                    value={employeeObject?.dateOfIssuanceCard ? moment(employeeObject?.dateOfIssuanceCard).format("YYYY-MM-DD") : ""}
                                    fullWidth
                                    variant="outlined"
                                    size="small"
                                    inputProps={{
                                        max: moment().format("YYYY-MM-DD"),
                                    }}
                                    InputLabelProps={{
                                        shrink: true,
                                    }}
                                    validators={["required"]}
                                    errorMessages={['this field is required', 'email is not valid']}
                                />
                            </Grid>
                            <Grid item lg={6} md={6} sm={12} xs={12}>
                                <TextValidator
                                    className="w-100 stylePlaceholder"
                                    size="small"
                                    label={
                                        <span className="font">
                                            <span className="text-red"> * </span>
                                            Nơi cấp
                                        </span>}
                                    variant="outlined"
                                    onChange={handleChangeValue}
                                    type="text"
                                    name="placeOfIssueCard"
                                    value={employeeObject.placeOfIssueCard}
                                    validators={["required"]}
                                    errorMessages={['this field is required', 'email is not valid']}
                                />
                            </Grid>
                            <Grid item lg={6} md={6} sm={12} xs={12}>
                                <TextValidator
                                    className="w-100 stylePlaceholder"
                                    size="small"
                                    label={
                                        <span className="font">
                                            <span className="text-red"> * </span>
                                            Dân tộc
                                        </span>}
                                    variant="outlined"
                                    onChange={handleChangeValue}
                                    type="text"
                                    name="ethnic"
                                    value={employeeObject.ethnic}
                                    validators={["required"]}
                                    errorMessages={['this field is required', 'email is not valid']}
                                />
                            </Grid>
                            <Grid item lg={6} md={6} sm={12} xs={12}>
                                <TextValidator
                                    className="w-100 stylePlaceholder"
                                    size="small"
                                    label={
                                        <span className="font">
                                            <span className="text-red"> * </span>
                                            Tôn giáo
                                        </span>}
                                    variant="outlined"
                                    onChange={handleChangeValue}
                                    type="text"
                                    name="religion"
                                    value={employeeObject.religion}
                                    validators={["required"]}
                                    errorMessages={['this field is required', 'email is not valid']}
                                />
                            </Grid>
                        </Grid>
                    </Grid>
                </ValidatorForm>
            </DialogContent>
            <DialogActions>
                <div className="flex flex-space-between flex-middle mt-10">
                    <Button
                        variant="contained"
                        className="mr-12"
                        color="secondary"
                        onClick={handleCloseDialog}
                    >
                        Hủy
                    </Button>
                    <Button
                        variant="contained"
                        className="mr-12"
                        color="primary"
                    >
                        Lưu
                    </Button>
                </div>
            </DialogActions>
        </Dialog>
    )
}

export default AddEmployeeDialog;

